#video{
     position: fixed;
     object-fit: cover;
     height: 100%;
     width: 100%;
     z-index: -1;
}

.hero{
    height: 100vh;
    width: 100%;
    object-fit: contain;
}

.hero .content{
    text-align: center;
    position: absolute;
    margin:  auto;
    width: 100%;
    top: 40vh;
    height: 100%;
}

.hero .content h1{
    font-size: 4rem;
    color: #fff;
}

.hero .content p{
    font-size: 1.8rem;
    font-weight: 200;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
}

.hero .btn{
    margin: 1rem .2rem;
}

@media screen and (max-width:640px) {
    .content h1{
        font-size: 3rem;
    }

    .content p{
        font-size: 1.4rem;
        margin-bottom: 1.6rem;
    }
}