.header{
    position: fixed;
    width: 100%;
    height: 90px;
    display: flex ;
    justify-content: space-between;
    align-items: center;
    padding: 1rem ;
    z-index: 1;
}
.header-bg{
    background-color: rgba(0,0,0,.85);
    transition: .5s;
}
.nav-menu {
    display: flex;

}
.nav-menu li{
    padding: 0 1rem;
}

.nav-menu a{
    font-size: 1.2rem;
    font-weight: 500 ;
}

.hambugger{
     display: none ;
}

@media screen and (max-width:1240px ) {
    .hambugger{
        display: block;
   } 

   .nav-menu{
    position: absolute;
    display: flex;
    flex-direction: column ;
    justify-content: center;
    align-items: center;
    top: 0%;
    left: 100%;
    width: 100%;
    height: 100vh;
    transition: .3s;
    z-index: -1;
    background: rgba(1, 0, 0, 0.801);
   }
   .nav-menu.active{
    left: 0;
   }

   .nav-menu li{
    padding: 1em 0;
   }

   .nav-menu a{
    font-size: 2rem;
   }
}