.training {
width: 100%;
margin: 3rem 0;
display: grid;
grid-template-columns: repeat(2,1fr);
height: 100%;
}

button{
    background-color: rgb(99, 99, 99);
    margin: 2em;
}


.training .left{
    text-align: center;
    margin: auto;
    padding: 1rem 4rem;
    max-width:600px ;
}


.training .left p{
    margin: 1.2rem 0;
}

.training .right{
    width: 100%;
}

.right .img-container{
    display: grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
}

.right .img{
    max-width: 60%;
    border: 1px solid #333;
}

.right .top{
    grid-column: 1/ span 8;
    grid-row: 1;
    padding:20% ;
    z-index: 6;
}


.right .bottom{
    grid-column: 4/ -1;
    grid-row: 1;
    z-index: 5;
}


@media screen and (max-width:1140px) {
    .training{
        grid-template-columns: 1fr;
    }

    .training .img{
        max-width: 80%;
    }
}